import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PoWithRequisitionReportComponent } from './po-with-requisition-report.component';

const routes: Routes = [
    {
        path: '', component: PoWithRequisitionReportComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class PoWithRequisitionReportRoutingModule { }



