import { NgModule,NO_ERRORS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { PoWithRequisitionReportRoutingModule } from './po-with-requisition-report-routing.module';
import { PoWithRequisitionReportComponent } from './po-with-requisition-report.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    PoWithRequisitionReportRoutingModule
  ],
  declarations: [
    PoWithRequisitionReportComponent
  ],
  schemas:[
    NO_ERRORS_SCHEMA
  ]
})
export class PoWithRequisitionReportModule { }



